@use '../../_variables.scss' as *;

.github-corner {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1000;
  width: 100px;
  height: 100px;
  fill: $color-white;
  color: $color-text;

  .octo-svg {
    position: absolute;
    top: 0;
    border: 0;
    right: 0;
  }

  .octo-arm {
    fill: currentColor;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }

  .octo-body {
    fill: currentColor;
  }

  &:hover .octo-arm {
    animation: octocat-wave 560ms ease-in-out;
  }

  /* Анимация для руки */
  @keyframes octocat-wave {
    0%, 100% {
      transform: rotate(0);
    }
    20%, 60% {
      transform: rotate(-25deg);
    }
    40%, 80% {
      transform: rotate(10deg);
    }
  }

  /* === Адаптация для мобильных === */
  @media (max-width: 500px) {
    .octo-arm {
      animation: none;
    }

    .octo-arm {
      animation: octocat-wave 560ms ease-in-out;
    }
  }
}
