@use '../../../_variables.scss' as *;

.map-section {
  display: flex;
  flex-direction: column;
  margin: 2rem;
  padding: 20px;
  background-color: var(--color-result-bg);
  color: var(--color-white);
  border-radius: 20px;
  box-shadow: 0 4px 8px var(--color-shadow);

  .map-title {
    font-family: 'TITLE', sans-serif;
    font-size: 30px;
    text-align: center;
    margin-bottom: 20px;
  }

  .map-container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #4C4945;
    padding: 15px;
    border-radius: 30px;
    box-shadow: 0 2px 4px var(--color-shadow);
    position: relative;

    .map {
      width: 100%;
      min-height: 500px;
      border-radius: 15px;
      position: relative;
    }
  }
}

// Map Styles
.map {
  height: 500px;
  width: 100%;
  border-radius: 15px;
}

// Responsive Styles
@media (max-width: 768px) {
  .map-title {
    font-size: 1rem;
  }
}
