@use '../../../_variables.scss' as *; // Assuming you have a variables file

/*===== Поиск =====*/
.search.container.section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* Стиль для стрелки в фильтре (select) */
#century-filter-wrapper::after {
  content: "\25BC";
  font-size: 18px;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  pointer-events: none;
  color: var(--color-primary);
}

/* Контейнер формы поиска */
.form {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 850px;
  background-color: var(--color-white);
  border: 1px solid var(--color-primary);
  border-radius: 15px;
  overflow: hidden;
  padding: 5px;
}

/* Внутренний контейнер формы */
.search-form {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 850px;
  background-color: var(--color-white);
  border-radius: 15px;
  overflow: hidden;

  select {
    border-left: 1px solid var(--color-primary);
  }

  form {
    border: 1px solid var(--color-primary);
  }
}

/* Поле ввода поиска */
.search-field {
  flex: 2;
  padding: 15px 20px;
  border: none;
  outline: none;
  font-size: 16px;
  font-family: 'Times New Roman', Times, serif;
  color: var(--color-text);
  background-color: transparent;

  &:first-of-type {
    border-radius: 15px 0 0 15px;
  }

  &::placeholder {
    font-style: italic;
    color: var(--color-text-alt);
  }
}

/*===== Кнопка поиска =====*/
#search-btn {
  padding: 10px 20px;
  background-color: var(--color-button-bg);
  color: var(--color-text-alt);
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  outline: none;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  box-shadow: 0 2px 6px var(--color-shadow);
  text-decoration: none;
  border: 2px solid transparent;

  &:hover,
  &:focus {
    background-color: var(--color-button-hover);
    color: var(--color-bg-alt);
    border-color: var(--color-bg-alt);
  }

  &:active,
  &.active {
    color: var(--color-bg-alt);
    background: linear-gradient(145deg, rgba(20, 20, 20, 0.9), rgba(35, 35, 35, 0.9));
  }
}

/*===== Результаты =====*/
#results {
  margin: 20px 0;
  background-color: var(--color-result-bg);
  color: var(--color-text-alt);
  border: 1px solid var(--color-bg);
  border-radius: 20px;
  padding: 20px;
  width: 100%;
  max-width: 900px;
  max-height: 400px;
  overflow: auto;

  font-family: 'Secession Text', serif;
  letter-spacing: 0.5px;
  box-shadow: 0 4px 8px var(--color-shadow);
  transition: border-color 0.3s ease, box-shadow 0.3s ease;

  div {
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 5px;
    transition: background 0.3s, transform 0.3s;

    &:hover {
      background: var(--color-result-hover-bg);
      color: var(--color-text);
      transform: translateY(-10px);
    }
  }

  .result-wrapper {
    margin-bottom: 15px;

    h4 {
      font-size: 18px;
      margin-bottom: 5px;
      color: var(--color-text-alt);
    }

    p {
      font-size: 16px;
      line-height: 1.5;
      margin-bottom: 10px;
      word-wrap: break-word;
      color: var(--color-text-alt);
    }
  }
}

/* Стиль для событий */
.event {
  border: 1px solid var(--color-result-hover-bg);
  padding: 15px;
  margin: 15px 0;
  border-radius: 5px;
  background: var(--color-bg-alt);
  color: var(--color-text-alt);
  transition: border-color 0.3s ease, background 0.3s ease, color 0.3s ease;

  &:hover {
    border-color: var(--color-result-hover-bg);
    background: #fafafae6;
    color: var(--color-hover);
  }
}

.message {
  justify-content: center;
}

/* Стиль для сообщений об ошибках */
#error-message {
  justify-content: center;
  margin: 20px 0;
  text-align: left;
  background-color: var(--color-result-bg);
  color: var(--color-text-alt);
  border-radius: 5px;
  padding: 20px;
  max-width: 900px;
  max-height: 400px;
  overflow: auto;
  font-family: 'TITLE', sans-serif;
  box-shadow: 0 4px 8px var(--color-shadow);
  border: 1px solid var(--color-result-hover-bg);
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

/* Стиль для сообщений о том, что события не найдены */
#not-found, #error-message {
  text-align: center;
  color: var(--color-error);
  background-color: var(--color-error-bg);
  border: 1px solid var(--color-error);
  border-radius: 5px;
  padding: 20px;
  margin: 20px 0;
  font-size: 26px;
  transition: all 0.3s ease-in-out;
}

/*===== Случайное событие =====*/
.random-event {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

/*===== Фильтр по поиску (Select и поле ввода фильтра) =====*/
#century-filter-wrapper {
  position: relative;
  width: 40px;
  overflow: hidden;
  transition: width 0.3s ease;

  &.open {
    width: 300px;

    #century-filter::after {
      content: "";
    }

    #century-filter::before {
      content: "Век: ";
      position: absolute;
      left: 10px;
      top: 50%;
      transform: translateY(-50%);
      font-size: 18px;
      color: var(--color-text);
    }
  }

  #century-filter {
    padding: 15px 20px;
    border: none;
    background-color: var(--color-white);
    font-size: 21px;
    font-family: 'TITLE', sans-serif;
    color: var(--color-text);
    outline: none;
    cursor: pointer;
    border-radius: 0;
    transition: background-color 0.3s ease, width 0.3s ease;
    white-space: nowrap;
    text-align: right;

    &::after {
      content: "\25BC";
      font-size: 18px;
      position: absolute;
      top: 50%;
      right: 10px;
      transform: translateY(-50%);
      pointer-events: none;
      color: var(--color-primary);
    }
  }
}

/* Дополнительное поисковое поле (фильтр) */
.filter-field {
  flex: 1.5;
  padding: 15px 20px;
  border: 1px solid var(--color-primary);
  border-left: none;
  outline: none;
  font-size: 16px;
  font-family: 'Times New Roman', Times, serif;
  color: var(--color-text);
  background-color: var(--color-white);
  border-radius: 0 15px 15px 0;
  transition: background-color 0.3s ease, border-color 0.3s ease;

  &:hover,
  &:focus {
    background-color: var(--color-result-hover-bg);
    border-color: #333;
  }

  &:active {
    background-color: #e9e9e9;
    border-color: var(--color-footer-bg);
  }
}

/*===== Адаптив для устройств =====*/
@media (max-width: 768px) {
  .form {
    flex-direction: column;
    align-items: center;
    width: 300px;
    border-radius: 10px;
    padding: 0;
  }

  .search-field,
  .filter-field,
  #search-btn {
    width: 80%;
    font-size: 14px;
    padding: 12px;
    border-radius: 0;
  }

  #search-btn {
    border-radius: 10px;
  }

  .filter-field {
    border-radius: 10px;
  }

  #century-filter {
    width: auto;
  }
}

@media (max-width: 600px) {
  #results {
    width: 100%;
    max-width: 300px;
    margin: 1rem auto;
    padding: 10px;
    max-height: 200px;
  }

  #results div {
    padding: 8px;
    margin: 5px 0;
    font-size: 14px;
    border-radius: 8px;
  }

  .result-wrapper h4 {
    font-size: 14px;
  }

  .result-wrapper p {
    font-size: 14px;
  }
}

@media (max-width: 768px) {
  .message.container {
    flex-direction: column;
    align-items: center;
  }

  #results div,
  .event,
  #error-message {
    padding: 10px;
    margin: 5px 0;
    font-size: 14px;
    border-radius: 10px;
  }

  #error-message {
    display: flex;
    position: inherit;
    width: 280px;
  }
}
