@use '../../_variables.scss' as *;

.main-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(10, 10, 10, 0.9);
  color: var(--color-footer-text);
  margin: 2rem;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 -4px 15px rgba(0, 0, 0, 0.7);
  text-align: center;
  flex-direction: column;

  .footer-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1000px;
    gap: 20px;
    flex-wrap: wrap;
  }

  .footer-logo {
    width: 100px;
    height: 100px;
    background-image: url('../../../public/assets/photo/icon/logo.png');
    background-size: cover;
    background-position: center;
    border-radius: 20%;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.5);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }

  .footer-info {
    max-width: 400px;
    text-align: center;

    p {
      margin: 0.5rem 0;
      font-size: 1em;
      opacity: 0.7;
      transition: opacity 0.3s ease;

      &:hover {
        opacity: 1;
      }
    }
  }

  footer a {
    color: $color-footer-text; 
    text-decoration: none;
    transition: color 0.3s ease, text-decoration 0.3s ease;

    &:hover {
      text-decoration: underline;
    }
  }

  .footer-link {
    color: $color-footer-text;
    font-weight: bold;
    opacity: 0.8;
    transition: opacity 0.3s ease;

    &:hover {
      color: $color-hover-text;
      opacity: 1;
    }
  }

  .footer-social {
    display: flex;
    gap: 15px;

    .social-link {
      img {
        width: 30px;
        height: 30px;
        transition: transform 0.3s ease, opacity 0.3s ease;

        &:hover {
          transform: scale(1.1);
          opacity: 0.8;
        }
      }
    }
  }

  /*=== Адаптация футера ===*/
  @media (max-width: 768px) {
    padding: 1.5rem;

    .footer-content {
      flex-direction: column;
      align-items: center;
      gap: 10px;
    }

    .footer-logo {
      display: none;
    }

    .footer-info p {
      font-size: 0.9em;
    }

    .footer-social .social-link img {
      width: 25px;
      height: 25px;
    }
  }
}
