@use '../../_variables.scss' as *;

.navigation.historical {
    margin: 20px auto;
    background-color: var(--color-bg-alt);
    padding: 10px 20px;
    border-radius: 10px;
    text-align: center;
    font-weight: bold;
    color: var(--color-text-alt);
    font-family: 'Times New Roman', Times, serif;
    transition: all 0.3s ease;

    .nav-list {
        list-style: none;
        display: flex;
        justify-content: space-around;
        padding: 0;
        margin: 0;
    }

    .nav-list li {
        margin: 0 10px;
    }

    .nav-list a {
        padding: 10px 20px;
        background-color: var(--color-button-bg);
        color: var(--color-text-alt);
        border-radius: 10px;
        cursor: pointer;
        transition: all 0.3s ease;
        outline: none;
        font-size: 16px;
        font-weight: bold;
        text-transform: uppercase;
        display: flex;
        align-items: center;
        justify-content: center;
        box-shadow: 0 2px 6px var(--color-shadow);
        text-decoration: none;
        border: 2px solid transparent;

        &:hover, &:focus {
            background-color: var(--color-button-hover);
            color: #F7E1C8;
            border-color: #F7E1C8;
        }

        &.active, &:active {
            color: var(--color-bg-alt);
            background: linear-gradient(145deg, rgba(20, 20, 20, 0.9), rgba(35, 35, 35, 0.9));
        }
    }
}

/* Кнопка мобильного меню */
.mobile-menu-toggle {
    display: none;
    position: fixed;
    top: 20px;
    right: 20px;
    background-color: var(--color-button-bg);
    color: var(--color-text-alt);
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    font-size: 18px;
    cursor: pointer;
}

/* Адаптивные стили для мобильных устройств */
@media (max-width: 810px) {
    .mobile-menu-toggle {
        display: block !important;
    }

    .navigation.historical {
        flex-direction: column;
        position: fixed;
        top: 60px;
        right: 20px;
        background-color: var(--color-bg-alt);
        width: 200px;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
        border-radius: 10px;
        padding: 10px;
        z-index: 999;
        opacity: 0;
        transform: translateY(-20px);
        transition: opacity 0.3s ease, transform 0.3s ease;
        pointer-events: none;

        &.active {
            display: flex;
            opacity: 1;
            transform: translateY(0);
            pointer-events: auto;
        }
    }

    .nav-list {
        flex-direction: column;
        gap: 10px;
    }

    .nav-list a {
        padding: 10px 0;
        font-size: 16px;
    }
}
