@use '../../_variables.scss' as *;

.main-header {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(10, 10, 10, 0.9); 
  color: $color-white;
  padding: 4rem 2rem;
  border-radius: 15px;
  margin: 2rem;
  box-shadow: 0 4px 15px $color-before;
}

.header-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 50px;
}

.header-img {
  width: 140px;
  height: 140px;
  background-image: url('./../../../public/assets/photo/icon/logo.png');
  background-size: cover;
  background-position: center;
  border-radius: 20%; 
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.5);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.header-img:hover {
  transform: translateY(-10px); 
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.7);
}

.header-text {
  max-width: 600px;
}

.header-title {
  font-size: 3.5em;
  font-weight: bold;
  font-family: 'TITLE', sans-serif;
  margin-bottom: 1rem;
  color: $color-header-title;
  border-bottom: 3px solid $color-header-title; 
  padding-bottom: 0.5rem;
}

.header-description p {
  font-size: 1.2em;
  font-family: 'DESC', sans-serif;
  line-height: 1.6;
  margin: 0;
  color: $color-header-description;
}

/*=== Адаптивность ===*/
@media (max-width: 768px) {
  .main-header {
    padding: 2rem;
    flex-direction: column;
    text-align: center;
  }

  .header-content {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  .header-img {
    display: none; 
  }

  .header-text {
    text-align: center;
  }

  .header-title {
    font-size: 3em;
  }

  .header-description p {
    font-size: 15px;
  }
}

@media (max-width: 810px) {
  .main-header {
    padding: 2rem;
    flex-direction: column;
    text-align: center; 
  }

  .header-content {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  .header-img {
    display: none;
  }

  .header-text {
    text-align: center;
  }

  .header-title {
    font-size: 2.8em;
  }

  .header-description p {
    font-size: 1em;
    line-height: 1.4;
  }
}
