@use '../../../_variables.scss' as *; // Assuming you have a variables file

/* Podcasts Section Styles */
.podcasts {
  display: flex;
  flex-direction: column;
  margin: 2rem;
  padding: 20px;
  background-color: var(--color-result-bg);
  color: var(--color-white);
  border-radius: 20px;
  box-shadow: 0 4px 8px var(--color-shadow);

  .podcast-title {
    font-family: 'TITLE', sans-serif;
    font-size: 30px;
    text-align: center;
  }

  .podcast-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;

    .podcast {
      width: calc(50% - 10px);
      background-color: #4c4945;
      padding: 15px;
      margin-bottom: 20px;
      border-radius: 15px;
      box-shadow: 0 2px 4px var(--color-shadow);

      button {
        padding: 10px 15px;
        background-color: var(--color-button-bg);
        color: var(--color-text-alt);
        border: none;
        border-radius: 10px;
        cursor: pointer;
        transition: all 0.3s ease;

        &:hover {
          background-color: var(--color-button-hover);
          color: var(--color-bg-alt);
        }
      }

      iframe {
        width: 100%;
        border-radius: 12px;
      }

      .photo-description {
        visibility: hidden;
        opacity: 0;
        position: absolute;
        bottom: 10px;
        left: 10px;
        right: 10px;
        background: rgba(0, 0, 0, 0.7);
        color: var(--color-white);
        padding: 1rem;
        border-radius: 10px;
        text-align: left;
        transition: visibility 0s, opacity 0.5s ease;
        transform: translateY(20px);

        h3 {
          font-size: 1.2rem;
          font-weight: bold;
          margin-bottom: 0.5rem;
        }

        p {
          font-size: 1rem;
          line-height: 1.4;
          margin-bottom: 0;
        }
      }
    }
  }
}

/* Mobile Adaptation Styles */
@media (max-width: 768px) {
  .podcast-list {
    flex-direction: column;
    width: 90%;
  }

  .podcast {
    width: 90% !important;
  }

  .podcast-title {
    font-size: 1rem;
  }

  .podcast_audio {
    width: 90%;
  }
}
